.contacts {
  &__wrapper {
    @include pageGrid
  }

  &__form {
  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active{
    -webkit-background-clip: text;
    -webkit-text-fill-color: $background-form-color;
    transition: background-color 5000s ease-in-out 0s;
    box-shadow: inset 0 0 20px 20px #F8F8FA;
  }
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }



    &__field, textarea {
      box-sizing: border-box;
      width: 100%;
      color: $background-form-color;
      font-size: 14px;
      font-family: Inter;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      padding: 14px 16px;
      border-width: 0;
      background-color: #F8F8FA;

      &:hover {
        border: 1px solid #D5D7DE;
      }

      &:focus {
        border: 1px solid $main-color-text;
      }
    }

    textarea {
      resize: none;
      border-radius: 16px;
    }

    &__field {
      height: 48px;
      margin-bottom: 16px;
      border-radius: 40px;
    }

    .primary-button {
      width: 100%;
      margin-top: 32px;

      @include onMobile {
        margin-bottom: 48px;
      }
    }
  }

  &__info {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / -1;
    }

    @include onDesktop {
      grid-column: 8 / 11;
    }
  }

  &__info {
    margin-bottom: 24px;


    &__label {
      color: #9FADC2;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      margin-bottom: 8px;
    }

    &__data {
      color: #3E3E3E;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      text-decoration: none;

      &:hover {
        color: #7E7E83;
      }
    }
    p {
      margin-bottom: 24px;
    }
  }
}


