.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #E8EFF9;
  font-weight: 700;
  text-transform: uppercase;
  z-index: 2;

  &__top {
    margin-bottom: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-color-text;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
